import React, { useState, useEffect, useContext } from "react";
import {
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  TextField,
  MenuItem,
  Button,
  useTheme,
} from "@mui/material";
import { useMutation, useQueryClient } from "react-query";
import { Formik } from "formik";
import { string, object } from "yup";
import { LoadingButton } from "@mui/lab";
import { updateUser } from "../api/userApi";
import _ from "lodash";

import Swal from "sweetalert2";
import { CustomContext } from "../context/provider/CustomProvider";

function EditUser({ open, setOpen }) {
  const queryClient = useQueryClient();
  const { customState } = useContext(CustomContext);
  const { palette } = useTheme();

  const [id, setId] = useState("");
  const [username, setUsername] = useState("");
  const [role, setRole] = useState("administrator");
  const [email, setEmail] = useState("");
  // const [password, setPassword] = useState("");
  // const [confirmPassword, setConfirmPassword] = useState("");

  useEffect(() => {
    if (!_.isEmpty(customState.userEditData)) {
      setId(customState?.userEditData?._id);
      setUsername(customState?.userEditData?.username);
      setEmail(customState?.userEditData?.email);
      setRole(customState?.userEditData?.role);
    }
  }, [customState.userEditData]);

  const initialValues = {
    id,
    username,
    email,
    role,
  };

  const validationSchema = object().shape({
    username: string()
      .trim()
      .required()
      .matches(
        /^[a-zA-Z0-9]+$/,
        "Username should not contain white spaces and special characters"
      ),
    email: string().trim().required().email(),
    role: string().trim().required(),
  });

  const editUserMutate = useMutation(updateUser);
  const onSubmit = (values, options) => {
    editUserMutate.mutateAsync(values, {
      onSettled: () => {
        queryClient.invalidateQueries(["user"]);
        options.setSubmitting(false);
      },
      onSuccess: async (data) => {
        if (data) {
          Swal.fire({
            icon: "success",
            iconColor: palette.primary.main,
            title: "Success",
            text: "User info updated!!!",
            confirmButtonColor: palette.primary.main,
          });

          options.resetForm();
        }
      },
      onError: (error) => {},
    });
  };

  return (
    <Dialog open={open} onClose={() => setOpen(false)} maxWidth="xs" fullWidth>
      <DialogTitle>Edit User Info</DialogTitle>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={onSubmit}
        enableReinitialize={true}
      >
        {({ errors, touched, handleSubmit, isSubmitting }) => (
          <>
            <DialogContent>
              <TextField
                
                label="Username"
                placeholder="Enter Username here"
                fullWidth
                size="small"
                value={username !== "" ? username : ""}
                onChange={(e) => setUsername(e.target.value)}
                error={Boolean(touched.username && errors.username)}
                helperText={errors.username && errors.username}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                type="email"
                
                label="Email Address"
                placeholder="Enter email address here"
                fullWidth
                size="small"
                value={email !== "" ? email : ""}
                onChange={(e) => setEmail(e.target.value)}
                error={Boolean(touched.email && errors.email)}
                helperText={errors.email && errors.email}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                select
                
                label="Select Role"
                placeholder="Role"
                fullWidth
                size="small"
                value={role}
                onChange={(e) => setRole(e.target.value)}
                error={Boolean(touched.role && errors.role)}
                helperText={errors.role && errors.role}
                sx={{ marginBottom: 2 }}
              >
                <MenuItem value="administrator">Administrator</MenuItem>
                {/* <MenuItem value="tutor">Tutor</MenuItem>
                      <MenuItem value="student">Student</MenuItem> */}
              </TextField>
              {/* <TextField
                
                type="password"
                label="Password"
                placeholder="Enter Password here"
                fullWidth
                size="small"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                error={Boolean(touched.password && errors.password)}
                helperText={errors.password && errors.password}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                
                type="password"
                label="Confirm Password"
                placeholder="Enter Password here"
                fullWidth
                size="small"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                error={Boolean(
                  touched.confirmPassword && errors.confirmPassword
                )}
                helperText={errors.confirmPassword && errors.confirmPassword}
                sx={{ marginBottom: 2 }}
              /> */}
            </DialogContent>
            <DialogActions>
              <LoadingButton
                loading={isSubmitting}
                variant="contained"
                onClick={handleSubmit}
              >
                Save Changes
              </LoadingButton>

              <Button color="secondary" onClick={() => setOpen(false)}>
                Cancel
              </Button>
            </DialogActions>
          </>
        )}
      </Formik>
    </Dialog>
  );
}

export default EditUser;
