import React, { useContext, useState } from "react";
import MaterialTable from "material-table";
import { Helmet } from "react-helmet-async";
import {
  Breadcrumbs,
  Box,
  Divider,
  Typography,
  Stack,
  Chip,
  Button,
  Alert,
} from "@mui/material";
import { useMutation, useQuery, useQueryClient } from "react-query";

import { Link } from "react-router-dom";
import {
  Menu,
  MenuButton,
  MenuItem as ReachMenuItem,
  MenuList,
} from "@reach/menu-button";
import {
  Add,
  DeleteSharp,
  DisabledByDefaultSharp,
  Edit,
  MoreVertSharp,
  RefreshSharp,
} from "@mui/icons-material";
import { disableUserAccount, getUsers } from "../components/api/userApi";

import Content from "../components/Content";
import { tableIcons } from "../assets/tableIcons";
import AddUser from "../components/modals/AddUser";
import EditUser from "../components/modals/EditUser";
import { CustomContext } from "../components/context/provider/CustomProvider";
import UpdateUserPassword from "../components/modals/UpdateUserPassword";

function Register() {
  const queryClient = useQueryClient();
  const { customDispatch } = useContext(CustomContext);

  const [openAddUser, setOpenAddUser] = useState(false);
  const [openEditUser, setOpenEditUser] = useState(false);
  const [openUpdateUserPassword, setOpenUpdateUserPassword] = useState(false);
  const [alertMessage, setAlertMessage] = useState({
    severity: "",
    msg: "",
  });

  //Get all users
  const users = useQuery(["user"], getUsers, {
    select: (data) => {
      return data.map((user) => {
        return {
          _id: user._id,
          username: user.username,
          email: user.email,
          role: user.role,
          active: user.active,
          createdAt: user.createdAt,
        };
      });
    },
  });

  const disableUserAccountMutate = useMutation(disableUserAccount);
  const handleDisableAccount = (rowData) => {
    const data = {
      id: rowData._id,
      active: !rowData.active,
    };

    disableUserAccountMutate.mutateAsync(data, {
      onSettled: () => {
        queryClient.invalidateQueries(["user"]);
      },
      onSuccess: (data) => {
        queryClient.invalidateQueries(["user"]);
        setAlertMessage({
          severity: "success",
          msg: data,
        });
      },
      onError: (error) => {
        setAlertMessage({
          severity: "error",
          msg: error,
        });
      },
    });
  };

  return (
    <>
      <Helmet>
        <title>AAMUSTED Results System | Register</title>
      </Helmet>
      <AddUser open={openAddUser} setOpen={setOpenAddUser} />
      <EditUser open={openEditUser} setOpen={setOpenEditUser} />
      <UpdateUserPassword
        open={openUpdateUserPassword}
        setOpen={setOpenUpdateUserPassword}
      />
      <section className="register-user-container">
        <Content>
          <Typography variant="h4"> New User</Typography>
          <Typography variant="body2">Add new user to the system</Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            <Breadcrumbs>
              <Link to="/info/register">Home</Link>
              <Typography>Register</Typography>
            </Breadcrumbs>
          </Box>
          <Divider sx={{ marginBottom: 2 }} />
          {alertMessage.msg && (
            <Alert
              severity={alertMessage.severity}
              onClose={() => setAlertMessage({ msg: "" })}
            >
              {alertMessage.msg}
            </Alert>
          )}
          <Box
            width="100%"
            display="flex"
            justifyContent="flex-end"
            paddingY={1}
          >
            <Button
              variant="contained"
              color="secondary"
              startIcon={<Add />}
              sx={{ paddingY: 1 }}
              onClick={() => setOpenAddUser(true)}
            >
              New User
            </Button>
          </Box>

          <Box>
            <MaterialTable
              icons={tableIcons}
              title="Users"
              data={users.data ? users.data : []}
              columns={[
                {
                  title: "ID",
                  field: "_id",
                  hidden: true,
                },
                {
                  title: "Username",
                  field: "username",
                },
                {
                  title: "Email Address",
                  field: "email",
                },
                {
                  title: "Role",
                  field: "role",
                },
                {
                  title: "Account Status",
                  field: "active",
                  render: (rowData) => (
                    <Chip
                      size="small"
                      label={rowData.active ? "Active" : "Disabled"}
                      color={rowData.active ? "success" : "error"}
                    />
                  ),
                },

                {
                  title: "Options",
                  sorting: false,
                  render: (rowData) => {
                    return (
                      <Menu>
                        <MenuButton
                          style={{
                            backgroundColor: "transparent",
                            outline: "none",
                            border: "none",
                          }}
                        >
                          <MoreVertSharp />
                        </MenuButton>
                        <MenuList
                          style={{
                            zIndex: "100",
                          }}
                        >
                          <ReachMenuItem
                            onSelect={() => handleDisableAccount(rowData)}
                          >
                            <DisabledByDefaultSharp fontSize="32px" />
                            <span>
                              {rowData.active === true
                                ? "Disable Account"
                                : "Enable Account"}
                            </span>
                          </ReachMenuItem>
                          <ReachMenuItem
                            onSelect={() => {
                              customDispatch({
                                type: "userId",
                                payload: rowData._id,
                              });
                              setOpenUpdateUserPassword(true);
                            }}
                          >
                            <Stack direction="row">
                              <Edit fontSize="32px" />
                              <span>Change Password</span>
                            </Stack>
                          </ReachMenuItem>
                          <ReachMenuItem
                            onSelect={() => {
                              customDispatch({
                                type: "EditUser",
                                payload: rowData,
                              });
                              setOpenEditUser(true);
                            }}
                          >
                            <Stack direction="row">
                              <Edit fontSize="32px" />
                              <span>Edit</span>
                            </Stack>
                          </ReachMenuItem>

                          <ReachMenuItem
                            onSelect={() => {
                              console.log("deleted");
                            }}
                          >
                            <DeleteSharp fontSize="32px" />
                            <span>Delete</span>
                          </ReachMenuItem>
                        </MenuList>
                      </Menu>
                    );
                  },
                },
              ]}
              isLoading={users.isLoading}
              style={{
                borderTop: `solid 2px #5aa7a7`,
                fontSize: "13px",
                boxShadow: `0px 0px 13px 0px rgb(82 63 105 / 10%)`,
                borderRadius: "10px",
              }}
              actions={[
                {
                  position: "toolbar",
                  tooltip: "Refresh",
                  icon: () => <RefreshSharp />,

                  isFreeAction: true,
                  onClick: () => {
                    users.refetch();
                  },
                },
              ]}
              options={{
                pageSizeOptions: [5, 10, 20, 50, 100],
                selection: true,
                exportButton: true,
              }}
            />
          </Box>
        </Content>
      </section>
    </>
  );
}

export default Register;
