import React, { useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  TextField,
  MenuItem,
  Button,
  useTheme,
} from "@mui/material";
import { useMutation, useQueryClient } from "react-query";
import { Formik } from "formik";
import { string, object, ref } from "yup";
import { LoadingButton } from "@mui/lab";
import { addUser } from "../api/userApi";
import Swal from "sweetalert2";

function AddUser({ open, setOpen }) {
  const queryClient = useQueryClient();
  const { palette } = useTheme();

  const [username, setUsername] = useState("");
  const [role, setRole] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const initialValues = {
    username,
    email,
    role,
    password,
    confirmPassword,
  };
  const validationSchema = object().shape({
    username: string()
      .trim()
      .required()
      .matches(
        /^[a-zA-Z0-9]+$/,
        "Username should not contain white spaces and special characters!"
      ),
    email: string().trim().required().email(),
    password: string()
      .trim()
      .required()
      .min(8, "Password Should be at least 8 characters long"),
    confirmPassword: string()
      .trim()
      .required()
      .oneOf([ref("password"), null], "Passwords do not match"),
  });

  const userMutate = useMutation(addUser);
  const onSubmit = (values, options) => {
    delete values.confirmPassword;
    userMutate.mutateAsync(values, {
      onSettled: () => {
        queryClient.invalidateQueries(["user"]);
        options.setSubmitting(false);
      },
      onSuccess: async (data) => {
        if (data) {
          Swal.fire({
            icon: "success",
            iconColor: palette.primary.main,
            title: "Success",
            text: "User added successfully!!",
            confirmButtonColor: palette.primary.main,
          });

          options.resetForm();
        }
      },
      onError: (error) => {
        Swal.fire({
          icon: "error",
          title: "Error",
          text: error,
          confirmButtonColor: "#f00",
        });
      },
    });
  };

  return (
    <Dialog open={open} onClose={() => setOpen(false)} maxWidth="xs" fullWidth>
      <DialogTitle>Add New User</DialogTitle>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={onSubmit}
        enableReinitialize={true}
      >
        {({ errors, touched, handleSubmit, handleBlur, isSubmitting }) => (
          <>
            <DialogContent>
              <TextField
                
                label="Username"
                placeholder="Enter Username here"
                fullWidth
                size="small"
                value={username}
                onBlur={handleBlur("username")}
                onChange={(e) => setUsername(e.target.value)}
                error={Boolean(touched.username && errors.username)}
                helperText={errors.username && errors.username}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                type="email"
                
                label="Email Address"
                placeholder="Enter email address here"
                fullWidth
                size="small"
                value={email}
                onBlur={handleBlur("email")}
                onChange={(e) => setEmail(e.target.value)}
                error={Boolean(touched.email && errors.email)}
                helperText={errors.email && errors.email}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                select
                
                label="Select Role"
                placeholder="Role"
                fullWidth
                size="small"
                value={role}
                onChange={(e) => setRole(e.target.value)}
                error={Boolean(touched.role && errors.role)}
                helperText={errors.role && errors.role}
                sx={{ marginBottom: 2 }}
              >
                <MenuItem value="administrator">Administrator</MenuItem>
                {/* <MenuItem value="tutor">Tutor</MenuItem>
                      <MenuItem value="student">Student</MenuItem> */}
              </TextField>
              <TextField
                
                type="password"
                label="Password"
                placeholder="Enter Password here"
                fullWidth
                size="small"
                value={password}
                onBlur={handleBlur("password")}
                onChange={(e) => setPassword(e.target.value)}
                error={Boolean(touched.password && errors.password)}
                helperText={errors.password && errors.password}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                
                type="password"
                label="Confirm Password"
                placeholder="Enter Password here"
                fullWidth
                size="small"
                value={confirmPassword}
                onBlur={handleBlur("confirmPassword")}
                onChange={(e) => setConfirmPassword(e.target.value)}
                error={Boolean(
                  touched.confirmPassword && errors.confirmPassword
                )}
                helperText={errors.confirmPassword && errors.confirmPassword}
                sx={{ marginBottom: 2 }}
              />
            </DialogContent>
            <DialogActions>
              <LoadingButton
                loading={isSubmitting}
                variant="contained"
                onClick={handleSubmit}
              >
                Register
              </LoadingButton>

              <Button color="secondary" onClick={() => setOpen(false)}>
                Cancel
              </Button>
            </DialogActions>
          </>
        )}
      </Formik>
    </Dialog>
  );
}

export default AddUser;
